body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Kumbh Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Kumbh Sans", sans-serif;
  color: #52504e;
}

h1 {
  color: #52504e;
}

h3 {
  color: #52504e;
  text-align: center;
}

li {
  font-family: "Kumbh Sans", sans-serif;
  color: #52504e;
  font-size: 17px;
}

.Main-Container {
  font-size: 20px;
  background-color: #efe2ba;
  height: 100vh;
  background-image: url("https://www.transparenttextures.com/patterns/cream-dust.png");
  display: flex;
  flex-direction: column;
  align-content: flex-start;
}

.List-Header {
  text-align: center;
  font-family: "Kumbh Sans", sans-serif;
}

.Input-Field {
  text-align: center;
  font-size: 40px;
  /* padding-top: 61px; */
  font-family: "Kumbh Sans", sans-serif;
}

.Nominate-Statement {
  color: #52504e;
  text-align: center;
}

.Notification {
  color: #9a2138;
}

.Nominated-Movie {
  list-style-type: none;
}

.Nominated-Movie-List {
  margin: 16px 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.05), 2px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 16px;
  width: 160px;
}

.Poster {
  max-width: 150px;
}

.Single-Movie {
  list-style-type: none;
}

.Loader {
  text-align: center;
  padding-top: 60px;
}

.Loader {
  text-align: center;
